<template>
  <div>
    <div class="task-new">
      <el-button @click="dialogFormVisible = true" type="info"></el-button>
    </div>
    <el-table :data="tableData" border style="width: 100%">
      <el-table-column type="index" label="" width="80"></el-table-column>
      <el-table-column property="1" label="" width="110"></el-table-column>
      <el-table-column property="2" label="">
        <template scope="scope">
          <div v-for="it in parse(scope.row[2])">{{it.touzhuresult}} {{it.bid_price_list}} {{it.top_price}} </div>
        </template>
      </el-table-column>
      <el-table-column property="3" label="" width="120"></el-table-column>
      <el-table-column property="4" label="ID"></el-table-column>
      <el-table-column property="5" label="" width="100">
        <template scope="scope">
          <el-tag :type="scope.row[5] == 0 ? 'success' : 'danger'">{{scope.row[5] == 0 ? '' :(scope.row[5] == 1?'':'')}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column property="6" label="" width="150"></el-table-column>
      <el-table-column property="8" label="" width="110">
        <template scope="scope">
          <div>{{scope.row[8]}}:{{scope.row[9]}}:{{scope.row[10]}}</div>
        </template>
      </el-table-column>
      <el-table-column label="" width="230">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)">{{scope.row[5] == 0 ? '' : ''}}</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form :model="form" label-width="120px" ref="form">
        <el-form-item prop="name" label="" :rules="{
                        required: true, message: '', trigger: 'blur'}">
          <el-input v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item prop="key" label="" :rules="{
                        required: true, message: '', trigger: 'blur'
                      }">
          <el-input v-model="form.key"></el-input>
        </el-form-item>
        <el-form-item prop="gn_id_list" label="ID" :rules="{
                        required: true, message: 'ID', trigger: 'blur'
                      }">
          <el-input v-model="form.gn_id_list" placeholder="id,"></el-input>
        </el-form-item>
        <el-form-item prop="loop_time" label="">
          <el-time-picker v-model="form.loop_time" placeholder="">
          </el-time-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>

var host = '/api'
export default {
  data() {
    return {
      dialogFormVisible: false,
      tableData: [],
      form: { loop_time: new Date() }
    }
  },
  created() {
    this.loadAll()

  },
  methods: {

    parse(it) {
      try {
        return JSON.parse(it)
      }
      catch (e) {

      }

    },
    loadAll() {
      this.$http.get(host + '/list').then((res) => {
        this.tableData = res.body;
      })
    },
    save() {
      var form = this.form;

      this.$refs['form'].validate((valid) => {
        if (valid) {
          var h = form.loop_time.getHours();
          var m = form.loop_time.getMinutes();
          var s = form.loop_time.getSeconds();
          this.$http.get(host + '/add/' + `${form.name}/${form.key}/${form.gn_id_list}/${h}/${m}/${s}`).then((res) => {
            this.$message({
              showClose: true,
              message: '',
              type: 'success'
            });
            this.dialogFormVisible = false;
            this.loadAll()
          }, () => {
            this.$message({
              showClose: true,
              message: '',
              type: 'error'
            });
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },
    handleEdit(index, row) {
      var id = row[0]
      if (this.tableData[index][5] == 0) {

        this.$http.get(host + '/pause/' + row[0]).then((res) => {
          this.$set(this.tableData[index], 5, 1);
          this.$nextTick()
        }, () => {
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          });
        })
      }
      else {

        this.$http.get(host + '/resume/' + row[0]).then((res) => {
          this.$set(this.tableData[index], 5, 0);
          this.$nextTick()
        }, () => {
          this.$message({
            showClose: true,
            message: '',
            type: 'error'
          });
        })
      }

    },
    handleDelete(index, row) {
      this.$http.get(host + '/remove/' + row[0]).then((res) => {
        this.tableData.splice(index, 1)
      }, () => {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      })
    }
  }
}
</script>
<style>
.task-new {
  float: right;
  margin: 20px;
}
</style>



